import '../ControlPanelStyle.css';
import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

var getDD;
var getSD;
var getSDY;
var getSDM;
var getSDD;
const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 250,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));
const useStylesd = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 74,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));
function SelectType() {
  const classes = useStyles();
  const [vacType, setVacType] = React.useState('');

  const handleChange = (event) => {
    setVacType(event.target.value);
    getDD = event.target.value;
  };
  return (
    <div>
      <FormControl className={classes.formControl}>
        <InputLabel id="vac-label">Atostogų rūšis</InputLabel>
        <Select
          labelId="vac-label"
          id="vac-id"
          value={vacType}
          onChange={handleChange}
        >
          <MenuItem value={"Kasmetinės atostogos"}>Kasmetinės atostogos</MenuItem>
          <MenuItem value={"Neapmokamos atostogos"}>Neapmokamos atostogos</MenuItem>
          <MenuItem value={"Kitos atostogos"}>Kitos atostogos</MenuItem>
        </Select>
      </FormControl>
    </div>
  );
}
function StartDate() {
  const classes = useStylesd();
  const [sYear, setStartYear] = React.useState('');
  const [sMonth, setStartMonth] = React.useState('');
  const [sDay, setStartDay] = React.useState('');

  const handleChangesy = (event) => {
    setStartYear(event.target.value);
    getSDY = event.target.value;
  };
  const handleChangesm = (event) => {
    setStartMonth(event.target.value);
    getSDM = event.target.value;
    if((getSDM < 10)&&(getSDM !== undefined)){getSDM = "0" + getSDM}
  };
  const handleChangesd = (event) => {
    setStartDay(event.target.value);
    getSDD = event.target.value;
    if((getSDD < 10)&&(getSDD !== undefined)){getSDD = "0" + getSDD} 
  };
  getSD = getSDY + "-" + getSDM + "-" + getSDD;
  var tempd = new Date();
  var yeard = tempd.getFullYear();
  return (
    <div>
      <FormControl className={classes.formControl}>
        <InputLabel id="sd-label">Metai</InputLabel>
        <Select
          labelId="sd-label"
          id="sYear"
          value={sYear}
          onChange={handleChangesy}
        >
          <MenuItem value={yeard}>{yeard}</MenuItem>
          <MenuItem value={yeard + 1}>{yeard + 1}</MenuItem>
        </Select>
      </FormControl>
      <FormControl className={classes.formControl}>
        <InputLabel id="sdm-label">Mėnesis</InputLabel>
        <Select
          labelId="sdm-label"
          id="sMonth"
          value={sMonth}
          onChange={handleChangesm}
        >
          <MenuItem value={1}>1</MenuItem>
          <MenuItem value={2}>2</MenuItem>
          <MenuItem value={3}>3</MenuItem>
          <MenuItem value={4}>4</MenuItem>
          <MenuItem value={5}>5</MenuItem>
          <MenuItem value={6}>6</MenuItem>
          <MenuItem value={7}>7</MenuItem>
          <MenuItem value={8}>8</MenuItem>
          <MenuItem value={9}>9</MenuItem>
          <MenuItem value={10}>10</MenuItem>
          <MenuItem value={11}>11</MenuItem>
          <MenuItem value={12}>12</MenuItem>
        </Select>
      </FormControl>
      <FormControl className={classes.formControl}>
        <InputLabel id="sdd-label">Diena</InputLabel>
        <Select
          labelId="sdd-label"
          id="sDay"
          value={sDay}
          onChange={handleChangesd}
        >
          <MenuItem value={1}>1</MenuItem>
          <MenuItem value={2}>2</MenuItem>
          <MenuItem value={3}>3</MenuItem>
          <MenuItem value={4}>4</MenuItem>
          <MenuItem value={5}>5</MenuItem>
          <MenuItem value={6}>6</MenuItem>
          <MenuItem value={7}>7</MenuItem>
          <MenuItem value={8}>8</MenuItem>
          <MenuItem value={9}>9</MenuItem>
          <MenuItem value={10}>10</MenuItem>
          <MenuItem value={11}>11</MenuItem>
          <MenuItem value={12}>12</MenuItem>
          <MenuItem value={13}>13</MenuItem>
          <MenuItem value={14}>14</MenuItem>
          <MenuItem value={15}>15</MenuItem>
          <MenuItem value={16}>16</MenuItem>
          <MenuItem value={17}>17</MenuItem>
          <MenuItem value={18}>18</MenuItem>
          <MenuItem value={19}>19</MenuItem>
          <MenuItem value={20}>20</MenuItem>
          <MenuItem value={21}>21</MenuItem>
          <MenuItem value={22}>22</MenuItem>
          <MenuItem value={23}>23</MenuItem>
          <MenuItem value={24}>24</MenuItem>
          <MenuItem value={25}>25</MenuItem>
          <MenuItem value={26}>26</MenuItem>
          <MenuItem value={27}>27</MenuItem>
          <MenuItem value={28}>28</MenuItem>
          <MenuItem value={29}>29</MenuItem>
          <MenuItem value={30}>30</MenuItem>
          <MenuItem value={31}>31</MenuItem>          
        </Select>
      </FormControl>
    </div>
  );
}

const Vacation = (props) => {
  var todayd = new Date();
  var today = "";
  today = today + todayd.getFullYear() + "-";
  if(todayd.getMonth() < 10){today = today + "0" + (todayd.getMonth() + 1) + "-"}
  else today = today + todayd.getMonth() + "-";
  if(todayd.getDate() < 10){today = today + "0" + todayd.getDate()}
  else today = today + todayd.getDate();
  return (
    <div className="dashboard-page">
        <h2>Atostogų prašymas</h2>
        <h2>{props.id}</h2>
        <br/>
        <div>
          {SelectType()}
          <br/>
          {StartDate()}
          {console.log(getDD)}
          {console.log(today)}
          {console.log(getSD)}
          <div id="date-error"/>
        </div>
    </div>
  );
};

export default Vacation;
